import { create } from 'zustand';

/**
 * Quiz Store
 * Quản lý state của quiz hiện tại
 */
export const useQuizStore = create((set) => ({
  currentLesson: null,
  questionCount: 10,
  quizSettings: {
    scoringMode: 'basic',
    timeLimit: 30,
    autoNext: true,
  },
  sharedQuizId: null,
  quizResult: null,

  setCurrentLesson: (lesson) => {
    set({ currentLesson: lesson });
  },

  setQuestionCount: (count) => {
    set({ questionCount: count });
  },

  setQuizSettings: (settings) => {
    set((state) => ({
      quizSettings: {
        ...state.quizSettings,
        ...settings,
      },
    }));
  },

  setQuizResult: (result) => {
    set({ quizResult: result });
  },

  setSharedQuizId: (quizId) => {
    set({ sharedQuizId: quizId });
  },

  clearQuiz: () => {
    set({
      currentLesson: null,
      questionCount: 10,
      quizSettings: {
        scoringMode: 'basic',
        timeLimit: 30,
        autoNext: true,
      },
      sharedQuizId: null,
      quizResult: null,
    });
  },
}));

